import { MpBox } from '@mekari/pixel-box';
import { MpButtonIcon } from '@mekari/pixel-button-icon';
import { createStyledAttrsMixin, isUndef, cloneVNode } from '@mekari/pixel-utils';

/**
 * Exported input properties.
 *
 * @prop {string|object} as - Component used for root node.
 * @prop {number|string} value - Value of input.
 * @prop {number|string} defaultValue - Initial value of input.
 * @prop {string} placeholder - Placeholder of input.
 * @prop {string} size - Size of input.
 * @prop {string} variant - Variant of input.
 * @prop {boolean} isReadOnly - If true, input be read only.
 * @prop {boolean} isDisabled - If true, input will be disabled.
 * @prop {boolean} isInvalid - If true, input is marked as invalid.
 * @prop {boolean} isRequired - If true, input will be required.
 * @prop {boolean} isFullWidth - If true, input will span full width of it parent.
 * @prop {boolean} isClearable - If true, allow to remove input content with clear icon.
 * @prop {string} ariaLabel - Optional prop for accessibility.
 * @prop {string} ariaLabelledBy - Optional prop for accessibility.
 */
const useInputProps = {
  as: {
    type: [String, Object],
    default: 'input'
  },
  value: [Number, String],
  defaultValue: [Number, String],
  placeholder: [String],
  size: {
    type: [String],
    default: 'md'
  },
  variant: {
    type: [String],
    default: 'outline'
  },
  isReadOnly: {
    type: [Boolean],
    default: false
  },
  isDisabled: {
    type: [Boolean],
    default: false
  },
  isInvalid: {
    type: [Boolean],
    default: false
  },
  isRequired: {
    type: [Boolean],
    default: false
  },
  isFullWidth: {
    type: [Boolean],
    default: true
  },
  isClearable: {
    type: [Boolean],
    default: false
  },
  ariaLabel: [String],
  ariaLabelledBy: [String]
};

/**
 * Exported input styles.
 */
const baseStyles = {
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  outline: '0',
  borderWidth: '1px',
  borderColor: 'inherit',
  borderRadius: 'sm',
  appearance: 'none',
  transition: 'all 250ms'
};

const sizeStyles = ({
  size,
  isFullWidth,
  stylePaddingLeft,
  stylePaddingRight
}) => {
  const variantStyles = {
    md: {
      width: isFullWidth ? '100%' : undefined,
      height: '9.5',
      minWidth: '22',
      fontSize: 'md',
      fontWeight: 'regular',
      lineHeight: 'md',
      borderRadius: 'md',
      paddingRight: stylePaddingRight,
      paddingLeft: stylePaddingLeft,
      paddingY: '2'
    },
    sm: {
      width: isFullWidth ? '100%' : undefined,
      height: '7.5',
      minWidth: '22',
      fontSize: 'sm',
      fontWeight: 'regular',
      lineHeight: 'md',
      borderRadius: 'sm',
      paddingRight: stylePaddingRight,
      paddingLeft: stylePaddingLeft,
      paddingY: '1'
    }
  };

  switch (size) {
    case 'md':
      return variantStyles['md'];

    case 'sm':
      return variantStyles['sm'];

    default:
      return variantStyles['md'];
  }
};

const componentStyles = ({
  variant,
  isHovered
}) => {
  const variantStyles = {
    outline: {
      color: 'dark',
      backgroundColor: 'white',
      borderColor: isHovered ? 'gray.400' : 'gray.100',
      _hover: {
        borderColor: 'gray.400'
      },
      _focus: {
        borderColor: 'blue.500',
        boxShadow: 'outer'
      },
      _disabled: {
        color: 'gray.400',
        backgroundColor: 'gray.50',
        borderColor: 'gray.100',
        cursor: 'not-allowed'
      },
      _invalid: {
        borderColor: 'red.400'
      },
      _readOnly: {
        userSelect: 'all'
      }
    },
    unstyled: {
      color: 'dark',
      backgroundColor: 'transparent',
      borderWidth: '0',
      borderRadius: 'none',
      height: undefined,
      minWidth: undefined,
      padding: undefined
    }
  };

  switch (variant) {
    case 'outline':
      return variantStyles['outline'];

    case 'unstyled':
      return variantStyles['unstyled'];

    default:
      return variantStyles['outline'];
  }
};

const useInputStyles = props => {
  return { ...baseStyles,
    ...sizeStyles(props),
    ...componentStyles(props)
  };
};

/**
 * Offers users a simple input for a form.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-input
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-input
 */

const MpInput = {
  name: 'MpInput',
  mixins: [createStyledAttrsMixin('MpInput')],
  inject: {
    $useFormControl: {
      default: null
    }
  },
  model: {
    prop: 'value',
    event: 'input'
  },
  props: { ...useInputProps
  },

  data() {
    return {
      currentValue: this.value !== null && this.value !== undefined ? this.value : this.defaultValue,
      isHovered: false,
      styleRight: 12,
      stylePaddingRight: 12,
      stylePaddingLeft: 12
    };
  },

  computed: {
    getFormControls() {
      if (!this.$useFormControl) {
        return {
          controlId: this.controlId,
          isReadOnly: this.isReadOnly,
          isDisabled: this.isDisabled,
          isInvalid: this.isInvalid,
          isRequired: this.isRequired
        };
      }

      return this.$useFormControl(this.$props);
    },

    componentStyles() {
      return useInputStyles({
        size: this.size,
        variant: this.variant,
        isHovered: this.isHovered,
        isFullWidth: this.isFullWidth,
        stylePaddingLeft: `${this.stylePaddingLeft}px`,
        stylePaddingRight: `${this.stylePaddingRight}px`
      });
    }

  },
  watch: {
    value(e) {
      this.currentValue = e;
    }

  },
  methods: {
    handleChange(e) {
      this.currentValue = e.target.value;
      this.$emit('input', e.target.value, e);
      this.$emit('change', e);
    },

    handleClear(e) {
      this.currentValue = null;
      this.handleChange(e);
      this.$emit('clear', e);
    }

  },

  mounted() {
    /**
     * Calculated horizontal padding if have any input addons prefix,
     * suffix or both and clear button icon position.
     * Only MpText and MpIcon allowed to be assign into MpInputAddon.
     */
    this.$parent.$children.map(parent => {
      let width = 0;
      const withBackground = isUndef(parent.$options.propsData.withBackground);
      parent.$children.map(child => {
        const children = (child.$slots.default || []).filter(e => e.tag);
        children.map(subchild => {
          if (subchild.componentOptions) {
            width = parseInt(getComputedStyle(subchild.elm).width, 10);
          }
        });
      });

      if (parent.$refs.inputLeftAddonRef) {
        this.stylePaddingLeft += withBackground ? width + 1 : width + 12;
      }

      if (parent.$refs.inputRightAddonRef) {
        this.stylePaddingRight += this.isClearable && 0;
        this.stylePaddingRight += withBackground ? width + 1 : width + 12;
        this.styleRight += withBackground ? width + 1 : width + 12;
      } else {
        this.stylePaddingRight += this.isClearable && 30;
      }
    });
  },

  render(h) {
    const controlId = this.getFormControls.controlId;
    return h(MpBox, {
      attrs: {
        role: 'group',
        position: 'relative',
        display: 'flex',
        alignItems: 'center',
        width: this.isFullWidth ? '100%' : undefined
      }
    }, [h(this.as, {
      class: [this.className],
      domProps: {
        value: this.currentValue
      },
      attrs: {
        placeholder: this.placeholder,
        id: controlId,
        readonly: this.isReadOnly || this.getFormControls.isReadOnly,
        disabled: this.getFormControls.isDisabled,
        required: this.getFormControls.isRequired,
        'aria-readonly': this.getFormControls.isReadOnly,
        'aria-disabled': this.getFormControls.isDisabled,
        'aria-invalid': this.getFormControls.isInvalid,
        'aria-required': this.getFormControls.isRequired,
        'aria-label': this.ariaLabel || controlId && `${controlId}-control-label`,
        'aria-labelledby': this.ariaLabelledBy || controlId && `${controlId}-help-text`,
        ...this.computedAttrs
      },
      on: { ...this.computedListeners,
        input: this.handleChange
      },
      ref: 'inputRef'
    }), !this.isReadOnly && !this.isDisabled && this.isClearable && h(MpButtonIcon, {
      props: {
        name: 'reset',
        size: 'sm',
        isHoverable: false
      },
      attrs: {
        position: 'absolute',
        right: `${this.styleRight}px`,
        width: '20px',
        height: '20px',
        minWidth: '20px',
        padding: '0',
        transition: 'all 250ms',
        visibility: 'hidden',
        opacity: '0',
        _groupHover: {
          visibility: this.currentValue && 'visible',
          opacity: this.currentValue && '1'
        }
      },
      on: {
        click: this.handleClear
      }
    })]);
  }

};

/**
 * Exported input addon properties.
 *
 * @prop {string} placement - Placement prefix and suffix component for input.
 * @prop {string} size - Size of input addon.
 * @prop {string} withBackground - If true, input addon will display background.
 */
const useInputAddonProps = {
  placement: {
    type: [String],
    default: 'left'
  },
  size: {
    type: [String],
    default: 'md'
  },
  withBackground: {
    type: [Boolean],
    default: true
  }
};

/**
 * A input addon allows addition of prefix and suffix of input.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-input
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-input
 */

const MpInputAddon = {
  name: 'MpInputAddon',
  mixins: [createStyledAttrsMixin('MpInputAddon')],
  props: { ...useInputAddonProps
  },
  computed: {
    componentStyles() {
      return {
        display: 'flex',
        alignItems: 'center',
        background: this.withBackground ? 'gray.50' : 'transparent',
        border: 'transparent',
        borderRadius: this.size === 'sm' ? '2xs' : 'sm',
        paddingX: '0',
        zIndex: '2',
        whiteSpace: 'nowrap',
        position: 'absolute',
        ...(this.placement === 'left' && {
          left: this.withBackground ? this.size === 'sm' ? '0.5' : '1' : '3',
          roundedRight: '0'
        }),
        ...(this.placement === 'right' && {
          right: this.withBackground ? this.size === 'sm' ? '0.5' : '1' : '3',
          roundedLeft: '0'
        })
      };
    }

  },

  render(h) {
    return h(this.as, {
      class: this.className,
      attrs: this.computedAttrs,
      on: this.computedListeners
    }, this.$slots.default);
  }

};
const MpInputLeftAddon = {
  name: 'MpInputLeftAddon',
  props: { ...useInputAddonProps
  },

  render(h) {
    return h(MpInputAddon, {
      props: { ...this.$props,
        placement: 'left'
      },
      attrs: {
        'data-pixel-component': 'MpInputLeftAddon',
        height: this.size === 'md' ? '7.5' : '6.5'
      },
      ref: 'inputLeftAddonRef'
    }, this.$slots.default);
  }

};
const MpInputRightAddon = {
  name: 'MpInputRightAddon',
  props: { ...useInputAddonProps
  },

  render(h) {
    return h(MpInputAddon, {
      props: { ...this.$props,
        placement: 'right'
      },
      attrs: {
        'data-pixel-component': 'MpInputRightAddon',
        height: this.size === 'md' ? '7.5' : '6.5'
      },
      ref: 'inputRightAddonRef'
    }, this.$slots.default);
  }

};

/**
 * Exported input group properties.
 *
 * @prop {string|object} as - Component used for root node.
 * @prop {string} size - Size of input group.
 */
const useInputGroupProps = {
  as: {
    type: [String, Object],
    default: 'div'
  },
  size: {
    type: [String],
    default: 'md'
  }
};

/**
 * A input group allows user to group a related collection of inputs in one visual region.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-input
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-input
 */

const MpInputGroup = {
  name: 'MpInputGroup',
  mixins: [createStyledAttrsMixin('MpInputGroup')],
  props: { ...useInputGroupProps
  },

  render(h) {
    const children = this.$slots.default.filter(item => item.tag);
    const cloneAllElement = children.map(node => {
      const cloneElement = cloneVNode(node, h);
      return h(cloneElement.componentOptions.Ctor, { ...cloneElement.data,
        on: cloneElement.componentOptions.listeners || {},
        props: { ...(cloneElement.data.props || {}),
          ...cloneElement.componentOptions.propsData,
          size: this.size || cloneElement.componentOptions.propsData.size
        },
        attrs: cloneElement.data.attrs
      }, node.componentOptions.children);
    });
    return h(MpBox, {
      props: {
        as: this.as
      },
      attrs: {
        position: 'relative',
        display: 'inline-flex',
        alignItems: 'center',
        width: '100%',
        ...this.splitProps.styleAttrs,
        ...this.computedAttrs
      }
    }, cloneAllElement);
  }

};

export { MpInput, MpInputGroup, MpInputLeftAddon, MpInputRightAddon };
