import { createStyledAttrsMixin, useId, runIfFn } from '@mekari/pixel-utils';

/**
 * Exported form control properties.
 *
 * @prop {string|object} as - Component used for root node.
 * @prop {boolean} controlId - Id of form control, prop is passed to its children.
 * @prop {boolean} isReadOnly - If true, prop is passed to its children.
 * @prop {boolean} isDisabled - If true, prop is passed to its children.
 * @prop {boolean} isInvalid - If true, prop is passed to its children.
 * @prop {boolean} isRequired - If true, prop is passed to its children.
 */
const useFormControlProps = {
  controlId: [String],
  isReadOnly: {
    type: [Boolean],
    default: false
  },
  isDisabled: {
    type: [Boolean],
    default: false
  },
  isInvalid: {
    type: [Boolean],
    default: false
  },
  isRequired: {
    type: [Boolean],
    default: false
  }
};

const MpFormControl = {
  name: 'MpFormControl',
  mixins: [createStyledAttrsMixin('MpFormControl')],
  props: {
    as: {
      type: [String, Object],
      default: 'div'
    },
    ...useFormControlProps
  },

  provide() {
    return {
      $formControlContext: () => this.props,
      $useFormControl: this.useFormControl
    };
  },

  inject: {
    $formControlContext: {
      default: null
    }
  },
  computed: {
    formControlContext() {
      if (!this.$formControlContext) return this.props;
      return this.$formControlContext();
    },

    props() {
      return {
        controlId: this.controlId || useId(3),
        isInvalid: this.isInvalid,
        isRequired: this.isRequired,
        isDisabled: this.isDisabled,
        isReadOnly: this.isReadOnly
      };
    }

  },
  methods: {
    useFormControl() {
      const context = this.formControlContext;
      if (!context) return this.props;
      const contextKeys = Object.keys(context);
      return contextKeys.reduce((previous, current) => {
        previous[current] = this.props[current];
        if (context && this.props[current] == null) previous[current] = context[current];
        return previous;
      }, {});
    }

  },

  render(h) {
    return h(this.as, {
      class: [this.className],
      attrs: {
        role: 'group',
        ...this.computedAttrs
      },
      on: this.computedListeners
    }, this.$scopedSlots.default({
      controlId: this.controlId,
      isInvalid: this.isInvalid,
      isRequired: this.isRequired,
      isDisabled: this.isDisabled,
      isReadOnly: this.isReadOnly
    }));
  }

};
const MpRequiredIndicator = {
  name: 'MpRequiredIndicator',
  mixins: [createStyledAttrsMixin('MpRequiredIndicator')],
  computed: {
    componentStyles() {
      return {
        color: 'red.400',
        marginLeft: '1'
      };
    }

  },

  render(h) {
    return h('span', {
      class: [this.className],
      attrs: {
        'aria-hidden': true,
        ...this.computedAttrs
      }
    }, '*');
  }

};
const MpFormLabel = {
  name: 'MpFormLabel',
  mixins: [createStyledAttrsMixin('MpFormLabel')],
  inject: {
    $useFormControl: {
      default: null
    }
  },
  props: { ...useFormControlProps
  },
  computed: {
    getFormControls() {
      return runIfFn(this.$useFormControl, this.$props);
    },

    componentStyles() {
      return {
        fontSize: 'md',
        fontWeight: 'semibold',
        marginBottom: '1',
        textAlign: 'left',
        verticalAlign: 'middle',
        display: 'flex'
      };
    }

  },

  render(h) {
    return h('label', {
      class: [this.className],
      attrs: {
        id: this.getFormControls.controlId ? `${this.getFormControls.controlId}-control-label` : null,
        for: this.getFormControls.controlId ? `${this.getFormControls.controlId}` : null,
        ...this.computedAttrs
      },
      on: this.computedListeners
    }, [...this.$slots.default, this.getFormControls.isRequired && h(MpRequiredIndicator)]);
  }

};
const MpFormHelpText = {
  name: 'MpFormHelpText',
  mixins: [createStyledAttrsMixin('MpFormHelpText')],
  inject: {
    $useFormControl: {
      default: null
    }
  },
  computed: {
    getFormControls() {
      return this.$useFormControl(this.$props);
    },

    componentStyles() {
      return {
        color: 'gray.600',
        fontSize: 'sm',
        marginTop: '1'
      };
    }

  },

  render(h) {
    return h('p', {
      class: [this.className],
      attrs: {
        id: this.getFormControls.controlId ? `${this.getFormControls.controlId}-help-text` : null,
        ...this.computedAttrs
      },
      on: this.computedListeners
    }, this.$slots.default);
  }

};
const MpFormErrorMessage = {
  name: 'MpFormErrorMessage',
  mixins: [createStyledAttrsMixin('MpFormErrorMessage')],
  inject: {
    $useFormControl: {
      default: null
    }
  },
  computed: {
    getFormControls() {
      return runIfFn(this.$useFormControl, this.$props);
    },

    componentStyles() {
      return {
        color: 'red.400',
        fontSize: 'sm',
        marginTop: '1',
        textAlign: 'left',
        verticalAlign: 'middle',
        display: 'flex'
      };
    }

  },

  render(h) {
    if (!this.getFormControls.isInvalid) return null;
    return h('p', {
      class: [this.className],
      attrs: {
        id: this.getFormControls.controlId ? `${this.getFormControls.controlId}-error-message` : null,
        ...this.computedAttrs
      }
    }, this.$slots.default);
  }

};

export { MpFormControl, MpFormErrorMessage, MpFormHelpText, MpFormLabel };
