const MpFragment = {
  name: 'MpFragment',
  directives: {
    fragment: {
      inserted(el) {
        const fragment = document.createDocumentFragment();
        Array.from(el.childNodes).forEach(child => fragment.appendChild(child));
        el.parentNode.insertBefore(fragment, el);
        el.parentNode.removeChild(el);
      }

    }
  },

  render(h) {
    return h('div', {
      directives: [{
        name: 'fragment'
      }]
    }, this.$slots.default);
  }

};

export { MpFragment };
