import { createStyledAttrsMixin, StringArray, SNA } from '@mekari/pixel-utils';

const spanFn = value => {
  if (Array.isArray(value)) {
    return value.map(v => v === 'auto' ? 'auto' : `span ${v}/span ${v}`);
  } else {
    return value === 'auto' ? 'auto' : `span ${value}/span ${value}`;
  }
};

const MpGridItem = {
  name: 'MpGridItem',
  mixins: [createStyledAttrsMixin('MpGridItem')],
  props: {
    colSpan: {
      type: StringArray
    },
    rowSpan: {
      type: StringArray
    },
    colStart: {
      type: StringArray
    },
    colEnd: {
      type: StringArray
    },
    rowStart: {
      type: StringArray
    },
    rowEnd: {
      type: StringArray
    }
  },
  computed: {
    componentStyles() {
      return {
        gridColumn: this.colSpan ? spanFn(this.colSpan) : null,
        gridRow: this.rowSpan ? spanFn(this.rowSpan) : null,
        gridColumnStart: this.colStart,
        gridColumnEnd: this.colEnd,
        gridRowStart: this.rowStart,
        gridRowEnd: this.rowEnd
      };
    }

  },

  render(h) {
    return h('div', {
      class: this.className,
      attrs: this.computedAttrs
    }, this.$slots.default);
  }

};
/**
 * Usage :** ` import { MpGrid } from '@mekari/pixel' `
 **/

const MpGrid = {
  name: 'MpGrid',
  mixins: [createStyledAttrsMixin('MpGrid')],
  props: {
    as: {
      type: String,
      default: 'div'
    },
    gap: SNA,
    rowGap: SNA,
    columnGap: SNA,
    autoFlow: SNA,
    autoRows: SNA,
    autoColumns: SNA,
    templateRows: SNA,
    templateColumns: SNA,
    templateAreas: SNA,
    area: SNA,
    column: SNA,
    row: SNA
  },
  computed: {
    componentStyles() {
      return {
        d: 'grid',
        gridArea: this.area,
        gridTemplateAreas: this.templateAreas,
        gridGap: this.gap,
        gridRowGap: this.rowGap,
        gridColumnGap: this.columnGap,
        gridAutoColumns: this.autoColumns,
        gridColumn: this.column,
        gridRow: this.row,
        gridAutoFlow: this.autoFlow,
        gridAutoRows: this.autoRows,
        gridTemplateRows: this.templateRows,
        gridTemplateColumns: this.templateColumns
      };
    }

  },

  render(h) {
    return h(this.as, {
      class: this.className,
      attrs: this.computedAttrs
    }, this.$slots.default);
  }

};

export { MpGrid, MpGridItem };
