import { extractListeners } from '@mekari/pixel-utils';
import { MpBox } from '@mekari/pixel-box';
import { MpAnimateHeight } from '@mekari/pixel-transition';

const MpCollapse = {
  name: 'MpCollapse',
  functional: true,
  props: {
    as: {
      type: [String, Object],
      default: 'div'
    },
    isOpen: Boolean,
    duration: {
      type: Number,
      default: 250
    },
    easing: {
      type: String,
      default: 'easeInOutSine'
    },
    startingHeight: Number,
    endingHeight: Number,
    animateOpacity: {
      type: Boolean,
      default: true
    }
  },

  render(h, {
    slots,
    props,
    data,
    listeners,
    ...rest
  }) {
    const children = slots().default;
    const nonNativeEvents = {
      start: e => {
        const emitStart = listeners.start;

        if (emitStart) {
          emitStart('start', e);
        }
      },
      finish: e => {
        const emitFinish = listeners.finish;

        if (emitFinish) {
          emitFinish('finish', e);
        }
      }
    };
    const {
      native,
      nonNative
    } = extractListeners({
      listeners
    }, nonNativeEvents);
    return h(MpAnimateHeight, { ...rest,
      props: {
        isOpen: props.isOpen,
        duration: props.duration,
        enterEasing: props.easing,
        leaveEasing: props.easing,
        initialHeight: props.startingHeight,
        finalHeight: props.endingHeight,
        animateOpacity: props.animateOpacity
      },
      on: nonNative,
      nativeOn: native,
      attrs: {
        'data-pixel-component': 'MpCollapse'
      }
    }, [h(MpBox, {
      props: {
        as: props.as
      },
      attrs: data.attrs
    }, children)]);
  }

};

export { MpCollapse };
