import { MountingPortal } from 'portal-vue';
import { canUseDOM, getSubstringAfterChar, useId } from '@mekari/pixel-utils';
import { MpNoSsr } from '@mekari/pixel-no-ssr';

const MpPortal = {
  name: 'MpPortal',
  props: {
    target: String,
    append: Boolean,
    unmountOnDestroy: Boolean,
    disabled: Boolean,
    name: String,
    order: Number,
    slim: Boolean,
    bail: Boolean,
    targetSlim: Boolean,
    as: {
      type: String,
      default: 'span'
    }
  },

  data() {
    return {
      portalTarget: undefined,
      targetId: undefined
    };
  },

  created() {
    if (!this.disabled) {
      this.mountTarget();
      this.unmountOnDestroy && this.$once('hook:destroyed', () => {
        canUseDOM && document.body.removeChild(this.portalTarget);
      });
    }
  },

  methods: {
    /**
     * @description Creates portal target node. If node doesn't exist, it is created and returned
     * @param {String} target
     * @returns {HTMLElement}
     */
    createPortalTarget(target, tag) {
      if (!canUseDOM) {
        return;
      }

      const existingPortalElement = document.querySelector(target);

      if (existingPortalElement) {
        return existingPortalElement;
      } else {
        const el = document.createElement(tag);

        if (target.startsWith('#')) {
          el.id = getSubstringAfterChar(target, '#');
        }

        if (target.startsWith('.')) {
          el.classList.add(getSubstringAfterChar(target, '.'));
          el.id = useId(4);
        }

        if (document.body != null) {
          document.body.appendChild(el);
        }

        return el;
      }
    },

    mountTarget() {
      if (!canUseDOM) {
        return;
      }

      this.portalTarget = this.createPortalTarget(this.target, this.as);
      this.targetId = this.portalTarget.id;
      this.$forceUpdate(); // Force re-render in case of changes.

      if (this.portalTarget && this.portalTarget.isConnected) {
        this.$nextTick(() => {
          this.$emit('portal:targetConnected');
        });
      }
    },

    unmountTarget() {
      if (!this.disabled) {
        canUseDOM && this.portalTarget.isConnected && document.body.removeChild(this.portalTarget);
      }
    }

  },

  render(h) {
    const children = this.$slots.default;
    return !this.disabled ? h(MpNoSsr, [h(MountingPortal, {
      props: {
        append: this.append,
        mountTo: `#${this.targetId}`,
        disabled: this.disabled,
        name: this.name,
        order: this.order,
        slim: this.slim,
        bail: this.bail,
        targetSlim: this.targetSlim
      },
      attrs: {
        'data-pixel-component': 'MpPortal'
      }
    }, children)]) : children[0];
  }

};

export { MpPortal };
