import { createStyledAttrsMixin, cloneVNodes, isDef, useId, forwardProps, createWatcher } from '@mekari/pixel-utils';
import { iconProps, MpIcon } from '@mekari/pixel-icon';
import { MpPseudoBox } from '@mekari/pixel-pseudo-box';
import { MpCollapse } from '@mekari/pixel-collapse';

const MpAccordion = {
  name: 'MpAccordion',
  mixins: [createStyledAttrsMixin('MpAccordion')],
  props: {
    allowMultiple: Boolean,
    allowToggle: Boolean,
    index: {
      type: Number,
      default: null
    },
    defaultIndex: {
      type: [Array, Number],
      default: 0
    }
  },

  data() {
    const initializeState = () => {
      if (this.allowMultiple) {
        return this.defaultIndex || [];
      } else {
        return this.defaultIndex || 0;
      }
    };

    return {
      expandedIndex: initializeState()
    };
  },

  computed: {
    isControlled() {
      return this.index != null;
    },

    _index: {
      get() {
        return this.isControlled ? this.index : this.expandedIndex;
      },

      set(val) {
        this.expandedIndex = val;
      }

    }
  },
  methods: {
    getExpandCondition(index, itemIndex) {
      if (Array.isArray(index)) {
        return index.includes(itemIndex);
      }

      return index === itemIndex;
    }

  },

  render(h) {
    const children = this.$slots.default.filter(e => e.tag);
    const cloned = cloneVNodes(children, h);
    const clones = cloned.map((vnode, index) => {
      const clone = h(vnode.componentOptions.Ctor, { ...vnode.data,
        ...(vnode.componentOptions.listeners || {}),
        props: { ...(vnode.data.props || {}),
          ...vnode.componentOptions.propsData,
          isOpen: this.getExpandCondition(this._index, index)
        },
        attrs: vnode.data.attrs || {},
        on: {
          change: isExpanded => {
            if (this.allowMultiple) {
              if (isExpanded) {
                const newIndices = [...this._index, index];

                if (!this.isControlled) {
                  this.expandedIndex = newIndices;
                }

                this.$emit('change', newIndices);
              } else {
                const newIndices = this._index.filter(itemIndex => itemIndex !== index);

                if (!this.isControlled) {
                  this.expandedIndex = newIndices;
                }

                this.$emit('change', newIndices);
              }
            } else if (isExpanded) {
              if (!this.isControlled) {
                this.expandedIndex = index;
              }

              this.$emit('change', index);
            } else if (this.allowToggle) {
              if (!this.isControlled) {
                this.expandedIndex = null;
              }

              this.$emit('change', null);
            }
          }
        }
      }, vnode.componentOptions.children);
      return clone;
    });
    return h('div', {
      class: this.className,
      attrs: this.computedAttrs,
      on: this.computedListeners
    }, clones);
  }

};
const MpAccordionItem = {
  name: 'MpAccordionItem',
  mixins: [createStyledAttrsMixin('MpAccordionItem', true)],
  props: {
    isOpen: {
      type: Boolean,
      default: null
    },
    defaultIsOpen: {
      type: Boolean,
      default: false
    },
    id: String,
    isDisabled: {
      type: Boolean,
      default: false
    }
  },

  provide() {
    return {
      $AccordionContext: () => this.AccordionContext
    };
  },

  data() {
    return {
      isExpanded: this.defaultIsOpen || false
    };
  },

  computed: {
    AccordionContext() {
      return {
        isExpanded: this._isExpanded,
        isDisabled: this.isDisabled,
        headerId: this.headerId,
        panelId: this.panelId,
        onToggle: this.onToggle
      };
    },

    isControlled() {
      return isDef(this.isOpen);
    },

    _isExpanded: {
      get() {
        return this.isControlled ? this.isOpen : this.isExpanded;
      },

      set(value) {
        this.isExpanded = value;
      }

    },

    _id() {
      return this.id || useId();
    },

    headerId() {
      return `accordion-header-${this._id}`;
    },

    panelId() {
      return `accordion-panel-${this._id}`;
    },

    componentStyles() {
      return {
        borderBottomWidth: '1px',
        _last: {
          borderBottomWidth: '1px'
        }
      };
    }

  },
  methods: {
    onToggle() {
      this.$emit('change', !this._isExpanded);

      if (!this.isControlled) {
        this._isExpanded = !this._isExpanded;
      }
    }

  },

  render(h) {
    return h(MpPseudoBox, {
      class: this.className,
      props: { ...forwardProps(this.$props) // borderTopWidth: '1px',
        // _last: { borderBottomWidth: '1px' }

      },
      attrs: this.computedAttrs
    }, [this.$scopedSlots.default({
      isExpanded: this._isExpanded,
      isDisabled: this.isDisabled
    })]);
  }

};
const MpAccordionHeader = {
  name: 'MpAccordionHeader',
  inheritAttrs: false,
  inject: ['$AccordionContext'],
  props: {
    isClickable: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    context() {
      return this.$AccordionContext();
    },

    computedAttrs() {
      return this.$data.attrs$;
    }

  },

  data() {
    return {
      attrs$: {}
    };
  },

  watch: {
    $attrs: createWatcher('attrs$')
  },

  render(h) {
    const {
      isExpanded,
      panelId,
      headerId,
      isDisabled,
      onToggle
    } = this.context;
    return h(MpPseudoBox, {
      attrs: {
        as: 'button',
        display: 'flex',
        alignItems: 'center',
        width: '100%',
        outline: 0,
        gap: 4,
        transition: 'all 0.2s',
        py: 3,
        _disabled: {
          opacity: '0.4',
          cursor: 'not-allowed'
        },
        id: headerId,
        type: 'button',
        disabled: isDisabled,
        'aria-disabled': isDisabled,
        'aria-expanded': isExpanded ? 'true' : 'false',
        'aria-controls': panelId,
        ...this.computedAttrs,
        'data-pixel-component': 'MpAccordionHeader',
        fontWeight: 'semibold',
        fontSize: 'md',
        color: 'dark',
        cursor: this.isClickable ? 'pointer' : 'default' // borderBottomWidth: '1px',
        // borderBottomColor: 'gray.100'
        // _last: { borderBottomWidth: '1px' }

      },
      on: this.computedListeners,
      nativeOn: {
        click: e => {
          e.stopPropagation();

          if (this.isClickable) {
            onToggle();
            this.$emit('click', e);
          }
        }
      }
    }, this.$slots.default);
  }

};
const MpAccordionPanel = {
  name: 'MpAccordionPanel',
  inheritAttrs: false,
  inject: ['$AccordionContext'],
  computed: {
    context() {
      return this.$AccordionContext();
    },

    computedAttrs() {
      return this.$attrs;
    }

  },

  render(h) {
    const {
      isExpanded,
      panelId,
      headerId
    } = this.context;
    return h(MpCollapse, {
      props: {
        isOpen: isExpanded
      },
      on: this.computedListeners,
      attrs: {
        py: 3,
        ...this.computedAttrs,
        id: panelId,
        'aria-labelledby': headerId,
        'aria-hidden': !isExpanded,
        role: 'region',
        'data-pixel-component': 'MpAccordionPanel'
      }
    }, this.$slots.default);
  }

};
const MpAccordionIcon = {
  name: 'MpAccordionIcon',
  mixins: [createStyledAttrsMixin('MpAccordionIcon')],
  inject: ['$AccordionContext'],
  props: {
    isClickable: {
      type: Boolean,
      default: true
    },
    ...iconProps
  },
  computed: {
    context() {
      return this.$AccordionContext();
    }

  },

  render(h) {
    const {
      isExpanded,
      isDisabled,
      onToggle
    } = this.context;
    return h(MpPseudoBox, {
      class: this.className,
      attrs: {
        cursor: this.isClickable ? 'pointer' : 'default'
      },
      on: this.computedListeners,
      nativeOn: {
        click: e => {
          e.stopPropagation();

          if (this.isClickable) {
            onToggle();
            this.$emit('click', e);
          }
        }
      }
    }, [h(MpIcon, {
      props: {
        size: this.size || '1.25em',
        name: this.name || 'caret-down'
      },
      attrs: {
        opacity: isDisabled ? 0.4 : 1,
        transform: isExpanded ? 'rotate(-180deg)' : null,
        transition: 'transform 0.2s',
        transformOrigin: 'center',
        ...this.computedAttrs
      }
    })]);
  }

};

export { MpAccordion, MpAccordionHeader, MpAccordionIcon, MpAccordionItem, MpAccordionPanel };
