import { isFunction } from 'lodash-es';
import { getElementById, cloneVNode, createStyledAttrsMixin, useId, isVueComponent, getElement } from '@mekari/pixel-utils';
import { MpFragment } from '@mekari/pixel-fragment';
import { MpPopper } from '@mekari/pixel-popper';
import { MpFlex } from '@mekari/pixel-flex';
import { MpPseudoBox } from '@mekari/pixel-pseudo-box';
import { MpIcon } from '@mekari/pixel-icon';

const MpPopoverTrigger = {
  name: 'MpPopoverTrigger',
  inheritAttrs: false,
  inject: ['$PopoverContext'],
  computed: {
    triggerId() {
      return this.context.popoverId + '-trigger';
    },

    context() {
      return this.$PopoverContext();
    },

    headerId() {
      return this.context.headerId;
    },

    bodyId() {
      return this.context.bodyId;
    },

    eventHandlers() {
      const {
        trigger
      } = this.context;

      if (trigger === 'click') {
        return {
          click: e => {
            this.$emit('click', e);
            this.context.toggleOpen();
          }
        };
      }

      if (trigger === 'hover') {
        return {
          focus: e => {
            this.$emit('focus', e);
            this.context.openPopover();
          },
          keydown: e => {
            this.$emit('keydown', e);

            if (e.key === 'Escape') {
              setTimeout(this.context.closePopover(), 300);
            }
          },
          blur: e => {
            this.$emit('blur', e);
            this.context.closePopover();
          },
          mouseenter: e => {
            this.$emit('mouseenter', e);
            this.context.set('isHovering', true);
            setTimeout(() => {
              this.context.openPopover();
            }, 300);
          },
          mouseleave: e => {
            this.$emit('mouseleave', e);
            this.context.set('isHovering', false);
            setTimeout(() => {
              if (this.context.isHovering === false) {
                this.context.closePopover();
              }
            }, 300);
          }
        };
      }
    }

  },

  mounted() {
    const {
      set
    } = this.context;
    this.$nextTick(() => {
      const triggerNode = getElementById(`${this.triggerId}`);

      if (!triggerNode) {
        console.warn('[pixel-ui]: Unable to locate PopoverTrigger node');
      } else {
        set('triggerNode', triggerNode);
      }
    });
  },

  render(h) {
    // const { popoverId } = this.context
    const children = this.$slots.default.filter(e => e.tag);
    if (!children) return console.error('[pixel-ui]: Popover Trigger expects at least one child');
    if (children.length && children.length > 1) return console.error('[pixel-ui]: Popover Trigger can only have a single child element');
    const cloned = cloneVNode(children[0], h);
    const {
      isOpen,
      popoverId,
      isManual
    } = this.context;
    const clone = h(cloned.componentOptions.Ctor, { ...cloned.data,
      ...(cloned.componentOptions.listeners || {}),
      props: { ...(cloned.data.props || {}),
        ...cloned.componentOptions.propsData
      },
      attrs: { ...cloned.data.attrs,
        ...this.$attrs,
        id: this.triggerId,
        'aria-haspopup': 'dialog',
        'aria-expanded': isOpen,
        'aria-controls': popoverId,
        'data-pixel-component': 'MpPopoverTrigger'
      },
      nativeOn: isManual ? null : this.eventHandlers
    }, cloned.componentOptions.children);
    return clone;
  }

};

const MpPopoverContent = {
  name: 'MpPopoverContent',
  inheritAttrs: false,
  inject: ['$PopoverContext', '$pixelColorMode'],
  props: {
    gutter: {
      type: [Number, String],
      default: 4
    },
    ariaLabel: String
  },
  computed: {
    context() {
      return this.$PopoverContext();
    },

    contentId() {
      return this.context.popoverId + '-content';
    },

    colorMode() {
      return this.$pixelColorMode();
    },

    eventHandlers() {
      const {
        trigger,
        handleBlur,
        closePopover,
        closeOnEscape,
        isManual
      } = this.context;
      let eventHandlers = {};

      if (trigger === 'click') {
        eventHandlers = {
          blur: e => {
            this.$emit('blur', e);
            handleBlur(e);
          }
        };
      }

      if (!isManual) {
        eventHandlers = { ...eventHandlers,
          mouseenter: e => {
            if (trigger === 'hover') {
              this.$emit('mouseenter', e);
              this.context.set('isHovering', true);
              setTimeout(this.context.openPopover(), 0);
            }
          },
          mouseleave: e => {
            const contentNode = getElementById(`pixel-popper_${this.contentId}`);
            contentNode.focus();

            if (trigger === 'hover') {
              this.$emit('mouseleave', e);
              this.context.set('isHovering', false);
              setTimeout(() => {
                if (this.context.isHovering === false) {
                  this.context.closePopover();
                }
              }, 0);
            }
          }
        };
      }

      eventHandlers = { ...eventHandlers,
        keydown: e => {
          this.$emit('keydown', e);

          if (e.key === 'Escape' && closeOnEscape) {
            closePopover && closePopover();
          }
        }
      };
      return eventHandlers;
    },

    portalEventHandlers() {
      const {
        isOpen,
        trigger,
        closePopover,
        isManual,
        usePortal
      } = this.context;
      let eventHandlers = {};

      if (usePortal && isOpen) {
        eventHandlers = { ...eventHandlers,
          close: e => {
            if (trigger === 'click' && !isManual) {
              closePopover();
            }
          },
          escape: e => {
            if (isOpen && trigger === 'click' && !isManual) {
              if (e.key === 'Escape') {
                closePopover();
              }
            }
          }
        };
      }

      return eventHandlers;
    },

    calculatedAttrs() {
      const {
        trigger
      } = this.context;

      if (trigger === 'click') {
        return {
          role: 'dialog',
          'aria-modal': 'false'
        };
      }

      if (trigger === 'hover') {
        return {
          role: 'tooltip'
        };
      }
    }

  },

  mounted() {
    const {
      set
    } = this.context;
    this.$nextTick(() => {
      const contentNode = getElementById(`pixel-popper_${this.contentId}`);

      if (!contentNode) {
        console.warn('[pixel-ui]: Unable to locate PopoverContent node');
      } else {
        set('contentNode', contentNode);
      }
    });
  },

  render(h) {
    const {
      isOpen,
      triggerNode,
      usePortal,
      placement,
      isForceUpdatePopper
    } = this.context;
    return h(MpPopper, {
      props: {
        as: 'section',
        usePortal,
        isOpen,
        placement,
        anchorEl: triggerNode,
        modifiers: [{
          name: 'offset',
          options: {
            offset: [0, this.gutter]
          }
        }],
        isForceUpdate: isForceUpdatePopper
      },
      attrs: {
        width: '100%',
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        ...this.$attrs,
        id: this.contentId,
        tabindex: -1,
        'aria-labelledby': this.headerId,
        'aria-describedby': this.bodyId,
        'aria-label': this.ariaLabel,
        'aria-hidden': !isOpen,
        outline: '0px solid transparent',
        zIndex: 'popover',
        ...this.calculatedAttrs,
        'data-pixel-component': 'MpPopoverContent'
      },
      nativeOn: this.eventHandlers,
      on: this.portalEventHandlers
    }, this.$slots.default);
  }

};

