const baseProps = {
  width: 'full',
  flex: ' 0 0 auto',
  userSelect: 'none'
};
const useListStyle = () => {
  return {
    pt: 3,
    pb: 2,
    alignItems: 'center'
  };
};

const interactionProps = ({
  isActive
}) => {
  return {
    bg: isActive ? 'ice.50' : 'white',
    color: 'dark',
    _hover: {
      bg: isActive ? 'ice.50' : 'gray.50',
      color: 'dark',
      outline: 0
    },
    _disabled: {
      color: 'gray.400',
      bg: 'white',
      cursor: 'not-allowed'
    }
  };
};

const useListItemStyle = props => {
  return { ...baseProps,
    ...interactionProps(props)
  };
};

