const MpPopoverList = {
  name: 'MpPopoverList',
  mixins: [createStyledAttrsMixin('MpPopoverList')],
  inject: ['$PopoverContext'],
  computed: {
    context() {
      return this.$PopoverContext();
    },

    listId() {
      return this.context.listId;
    },

    colorMode() {
      return this.$pixelColorMode();
    },

    componentStyles() {
      return useListStyle();
    }

  },
  methods: {
    handleKeyDown(event) {
      const {
        activeIndex: index,
        focusAtIndex,
        focusOnFirstItem,
        focusOnLastItem,
        closeDropdown,
        focusableItems
      } = this.context;
      const count = focusableItems.length;
      let nextIndex;

      if (event.key === 'ArrowDown') {
        event.preventDefault();
        nextIndex = (index + 1) % count;
        focusAtIndex(nextIndex);
      } else if (event.key === 'ArrowUp') {
        event.preventDefault();
        nextIndex = (index - 1 + count) % count;
        focusAtIndex(nextIndex);
      } else if (event.key === 'Home') {
        focusOnFirstItem();
      } else if (event.key === 'End') {
        focusOnLastItem();
      } else if (event.key === 'Tab') {
        event.preventDefault();
      } else if (event.key === 'Escape') {
        closeDropdown();
      } // Set focus based on first character


      if (/^[a-z0-9_-]$/i.test(event.key)) {
        event.stopPropagation();
        event.preventDefault();
        const foundNode = focusableItems.find(item => item.textContent.toLowerCase().startsWith(event.key));

        if (foundNode) {
          nextIndex = focusableItems.indexOf(foundNode);
          focusAtIndex(nextIndex);
        }
      }

      this.$emit('keydown', event);
    },

    handleBlur(event) {
      const {
        dropdownNode,
        buttonNode,
        isOpen,
        closeOnBlur,
        closeDropdown
      } = this.context;

      if (closeOnBlur && isOpen && dropdownNode && buttonNode && !dropdownNode.contains(event.relatedTarget) && !buttonNode.contains(event.relatedTarget)) {
        closeDropdown();
      }

      this.$emit('blur', event);
    }

  },

  render(h) {
    return h(MpFlex, {
      class: this.className,
      attrs: {
        id: this.listId,
        flexDirection: 'column',
        ...this.computedAttrs,
        'data-pixel-component': 'MpPopoverList'
      },
      on: this.computedListeners
    }, this.$slots.default);
  }

};
const MpPopoverListItem = {
  name: 'MpPopoverListItem',
  inheritAttrs: false,
  inject: ['$PopoverContext', '$pixelTheme', '$pixelColorMode'],
  props: {
    isDisabled: Boolean,
    isActive: Boolean,
    isArrow: Boolean,
    role: {
      type: String,
      default: 'dropdownitem'
    },
    as: {
      type: [String, Object],
      default: 'button'
    }
  },
  computed: {
    context() {
      return this.$PopoverContext();
    },

    listItemStyles() {
      return props => useListItemStyle(props);
    },

    theme() {
      return this.$pixelTheme();
    },

    colorMode() {
      return this.$pixelColorMode();
    }

  },

  render(h) {
    const {
      closeOnSelect,
      closeDropdown
    } = this.context;
    return h(MpPseudoBox, {
      props: {
        as: this.as
      },
      attrs: {
        px: 3,
        py: 2,
        width: 'full',
        textDecoration: 'none',
        color: 'dark',
        alignItems: 'center',
        textAlign: 'left',
        outline: 'none',
        'font-size': 'md',
        ...this.listItemStyles({
          theme: this.theme,
          colorMode: this.colorMode,
          isActive: this.isActive
        }),
        ...this.$attrs,
        role: this.role,
        tabindex: -1,
        disabled: this.isDisabled,
        'aria-disabled': this.isDisabled,
        'data-pixel-component': 'MpPopoverListItem'
      },
      nativeOn: {
        click: event => {
          this.$emit('click', event);

          if (this.isDisabled) {
            event.stopPropagation();
            event.preventDefault();
            return;
          }

          if (closeOnSelect) {
            closeDropdown();
          }
        }
      }
    }, [h(MpFlex, {
      attrs: {
        'justify-content': 'space-between',
        'align-items': 'center',
        width: 'full'
      }
    }, [this.$slots.default, this.isArrow && h(MpIcon, {
      props: {
        name: 'chevrons-right',
        size: 'sm'
      }
    })])]);
  }

};

const MpPopover = {
  name: 'MpPopover',

  provide() {
    return {
      $PopoverContext: () => this.PopoverContext
    };
  },

  props: {
    id: {
      type: String
    },
    defaultIsOpen: Boolean,
    isOpen: Boolean,
    returnFocusOnClose: {
      type: Boolean,
      default: false
    },
    initialFocusRef: [Object, String, Function],
    trigger: {
      type: String,
      default: 'click'
    },
    triggerId: {
      type: String,
      default: ''
    },
    closeOnBlur: {
      type: Boolean,
      default: true
    },
    closeOnEscape: {
      type: Boolean,
      default: true
    },
    usePortal: {
      type: Boolean,
      default: false
    },
    placement: {
      type: String,
      default: 'bottom-start'
    },
    isManual: {
      type: Boolean,
      default: false
    },
    isForceUpdatePopper: Boolean
  },
  computed: {
    PopoverContext() {
      return {
        set: this.set,
        isOpen: this._isOpen,
        closePopover: this.closePopover,
        openPopover: this.openPopover,
        toggleOpen: this.toggleOpen,
        triggerNode: this.triggerNode,
        contentNode: this.contentNode,
        setTriggerNode: this.setTriggerNode,
        trigger: this.trigger,
        isHovering: this.isHovering,
        handleBlur: this.handleBlur,
        closeOnEscape: this.closeOnEscape,
        popoverId: this.computedId,
        headerId: this.headerId,
        bodyId: this.bodyId,
        listId: this.listId,
        triggerId: this.triggerId,
        usePortal: this.usePortal,
        placement: this.placement,
        isManual: this.isManual,
        isForceUpdatePopper: this.isForceUpdatePopper
      };
    },

    isControlled() {
      // if (this.triggerId) {
      //   return false
      // }
      return this.isOpen !== false;
    },

    _isOpen: {
      get() {
        return this.isControlled ? this.isOpen : this.isOpenValue;
      },

      set(value) {
        this.isOpenValue = value;
      }

    },

    _initialFocusRef() {
      return isFunction(this.initialFocusRef) ? this.getNode(this.initialFocusRef()) : this.getNode(this.initialFocusRef);
    },

    headerId() {
      return `${this.computedId}-header`;
    },

    listId() {
      return `${this.computedId}-list`;
    },

    bodyId() {
      return `${this.computedId}-body`;
    },

    computedId() {
      return this.id || `popover-id-${useId()}`;
    }

  },

  data() {
    return {
      isOpenValue: this.defaultIsOpen || false,
      triggerNode: undefined,
      contentNode: undefined,
      prevIsOpen: false,
      isHovering: false
    };
  },

  mounted() {
    this.$watch('_isOpen', (_newVal, oldVal) => {
      this.prevIsOpen = oldVal;
    }, {
      immediate: true
    });
    this.$watch(vm => [vm._isOpen, vm._initialFocusRef, vm.trigger, vm.contentNode, vm.triggerNode, vm.prevIsOpen, vm.returnFocusOnClose], () => {
      if (this._isOpen && this.trigger === 'click') {
        /**
         * Caveat here:
         * Until Vue 3 is release, using it's $refs as props may not always return a value
         * in the props unless the consumer component updates it's context. This is because
         * Vue asynchronously updtaes the DOM and is also not reactive.
         *
         * Where this doesnt' work, we fallback to using an element selector to query
         * the element from the DOM. And use it as the initial focus ref.
         *
         * Work-around could be to use plain old JS selectors
         */
        setTimeout(() => {
          if (this._initialFocusRef) {
            this._initialFocusRef.focus();
          } else if (this.contentNode) {
            this.contentNode.focus();
          }
        });
      }

      if (!this._isOpen && this.prevIsOpen && this.trigger === 'click' && this.returnFocusOnClose) {
        if (this.triggerNode) {
          this.triggerNode.focus();
        }
      }
    });
  },

  methods: {
    closePopover() {
      if (!this.isControlled) {
        this._isOpen = false;
      }

      if (this.triggerId) {
        this._isOpen = false;
      }

      this.$emit('close');
    },

    openPopover() {
      if (!this.isControlled) {
        this._isOpen = true;
      }

      this.$emit('open');
    },

    toggleOpen() {
      if (!this.isControlled) {
        this._isOpen = !this._isOpen;
      }

      if (this._isOpen !== true) {
        this.$emit('open');
      } else {
        this.$emit('close');
      }
    },

    handleBlur(event) {
      let triggerNode = this.triggerNode;

      if (!triggerNode) {
        triggerNode = getElementById(this.triggerId);
      }

      if (!triggerNode) {
        console.warn('[pixel-ui]: Unable to locate PopoverTrigger node');
      } else {
        this.set('triggerNode', triggerNode);
      }

      if (this._isOpen && this.closeOnBlur && this.contentNode && triggerNode && !this.contentNode.contains(event.relatedTarget) && !triggerNode.contains(event.relatedTarget)) {
        this.closePopover();
      }
    },

    getNode(element) {
      if (typeof element === 'object') {
        const isVue = isVueComponent(element);
        return isVue ? element.$el : element;
      } else if (typeof element === 'string') {
        return getElement(element);
      }

      return null;
    },

    set(prop, value) {
      this[prop] = value;
      return this[prop];
    }

  },

  render(h) {
    return h(MpFragment, {
      attrs: {
        'data-pixel-component': 'MpPopover'
      }
    }, [this.$scopedSlots.default({
      isOpen: this._isOpen,
      onClose: this.closePopover
    })]);
  }

};

export { MpPopover, MpPopoverContent, MpPopoverHeader, MpPopoverList, MpPopoverListItem, MpPopoverPanel, MpPopoverTrigger };
