/**
 * Avatar component is used to represent a user, and displays the profile picture, initials or fallback icon.
 *
 * @category Data display - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-display-avatar
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-avatar
 */

const MpAvatar = {
  name: 'MpAvatar',
  mixins: [createStyledAttrsMixin('MpAvatar')],
  props: { ...useAvatarProps
  },

  data() {
    return {
      image: undefined,
      hasLoaded: false
    };
  },

  computed: {
    componentStyles() {
      return useAvatarStyles({
        size: this.size,
        name: this.name,
        showBorder: this.showBorder,
        borderColor: this.borderColor,
        backgroundColor: checkBackgroundColor(this.backgroundColor) ? this.backgroundColor : getRandomBackgroundColor() || getRandomBackgroundColor(),
        isClickable: !!(this.$listeners && this.$listeners.click),
        theme: this.theme,
        isSquare: this.isSquare
      });
    }

  },

  created() {
    const inBrowser = typeof window !== 'undefined';
    if (inBrowser) this.loadImage(this.src);
  },

  methods: {
    loadImage(src) {
      if (src) {
        if (!canUseDOM) return;
        const image = new window.Image();
        image.src = src;

        image.onload = e => {
          this.hasLoaded = true;
          this.$emit('load', e);
        };

        image.onerror = e => {
          this.hasLoaded = false;
          this.$emit('error', e);
        };
      }
    }

  },

  render(h) {
    const renderChildren = () => {
      if (this.icon) {
        const sizeStyles = {
          sm: {
            height: '4',
            width: '4'
          },
          md: {
            height: '5',
            width: '5'
          },
          lg: {
            height: '8',
            width: '8'
          },
          xl: {
            height: '16',
            width: '16'
          }
        };
        return h(MpIcon, {
          props: {
            name: this.icon,
            variant: this.iconVariant,
            color: this.iconColor
          },
          attrs: sizeStyles[this.size]
        });
      }

      if (this.src && this.hasLoaded && !this.icon) {
        const roundedStyles = {
          sm: 'sm',
          md: 'md',
          lg: '8px',
          xl: '12px'
        };
        return h(MpBox, {
          props: {
            as: 'img'
          },
          attrs: {
            width: '100%',
            height: '100%',
            borderRadius: this.isSquare ? roundedStyles[this.size] : 'full',
            objectFit: 'cover',
            alt: this.name,
            src: this.src
          }
        });
      }

      if (!this.src || this.src && !this.hasLoaded) {
        if (this.name) {
          return h(MpAvatarName, {
            props: {
              name: this.name
            },
            attrs: {
              width: this.lineHeight,
              height: this.lineHeight
            }
          });
        } else {
          return h(MpDefaultAvatar, {
            attrs: {
              width: '100%',
              height: '100%',
              'aria-label': this.name
            }
          });
        }
      }
    };

    return h('div', {
      class: this.className,
      attrs: this.computedAttrs,
      on: this.computedListeners
    }, [renderChildren(), this.$slots.default]);
  }

};

/**
 * Exported avatar group properties.
 *
 * @prop {number|string|array} groupSize - Size of avatar.
 * @prop {number|string|array} size - Size of avatar.
 * @prop {string|array} borderColor - Border color of avatar.
 * @prop {number|string|array} max - Displayed maximum avatar.
 * @prop {number|string|array} spacing - Space between each avatar.
 */
const useAvatarGroupProps = {
  groupSize: [Number, String, Array],
  // **deprecated**
  size: {
    type: [Number, String, Array],
    default: 'md'
  },
  borderColor: [String, Array],
  max: [Number, String, Array],
  spacing: {
    type: [Number, String, Array],
    default: -2
  }
};

const MpMoreAvatarLabel = {
  name: 'MpMoreAvatarLabel',
  props: {
    size: [String, Array],
    label: [String],
    isClickable: [Boolean]
  },

  render(h) {
    const sizeStyles = {
      sm: {
        width: 6,
        height: 6,
        lineHeight: 'sm',
        fontSize: 'sm'
      },
      md: {
        width: 8,
        height: 8,
        lineHeight: 'md',
        fontSize: 'md'
      },
      lg: {
        width: 12,
        height: 12,
        lineHeight: 'lg',
        fontSize: 'lg'
      },
      xl: {
        width: 20,
        height: 20,
        lineHeight: 'xl',
        fontSize: '2xl'
      }
    };
    return h(MpFlex, {
      props: {
        as: 'div'
      },
      attrs: {
        marginLeft: 2,
        align: 'center',
        justify: 'center',
        borderRadius: 'full',
        fontWeight: 'semibold',
        color: 'blue.400',
        backgroundColor: 'blue.50',
        userSelect: 'none',
        cursor: 'pointer',
        ...sizeStyles[this.size],
        'data-pixel-component': 'MpMoreAvatarLabel'
      },
      on: this.$listeners
    }, this.label);
  }

};
const MpAvatarGroup = {
  name: 'MpAvatarGroup',
  props: { ...useAvatarGroupProps
  },
  methods: {
    handleMoreClick(e) {
      this.$emit('click-more', e);
    }

  },

  created() {
    if (typeof this.groupSize !== 'undefined') console.error('[Pixel error]: Deprecated on `groupSize` prop and use `size` instead.');
  },

  render(h) {
    const children = this.$slots.default.filter(e => e.tag);
    const count = children.length;
    const max = parseInt(this.max, 10);
    const cloneAllElement = children.map((node, index) => {
      const isFirstELement = index === 0;

      if (!this.max || max && index < max) {
        let attrs = {};
        let propsData = {};
        let children = [];

        if (node.componentOptions.tag === 'mp-avatar') {
          attrs = {
            ml: isFirstELement ? 0 : this.spacing,
            zIndex: index
          };
          propsData = {
            size: this.size || this.groupSize,
            showBorder: true,
            borderColor: this.borderColor || attrs.borderColor
          };
        }

        if (node.componentOptions.tag === 'mp-tooltip' && node.componentOptions.children) {
          let firstChildrenNode = node.componentOptions.children[0];
          const childrenAttrs = {
            ml: isFirstELement ? 0 : this.spacing,
            zIndex: index
          };
          firstChildrenNode.data.attrs = { ...firstChildrenNode.data.attrs,
            ...childrenAttrs
          };
          const childrenPropsData = {
            size: this.size || this.groupSize,
            showBorder: true,
            borderColor: this.borderColor || attrs.borderColor
          };
          firstChildrenNode.componentOptions.propsData = { ...firstChildrenNode.componentOptions.propsData,
            ...childrenPropsData
          };
          children.push(firstChildrenNode);
        }

        node.componentOptions.propsData = { ...node.componentOptions.propsData,
          ...propsData
        };
        node.componentOptions.children = children.length === 1 ? children : undefined;
        node.data.attrs = { ...node.data.attrs,
          ...attrs
        };
        return node;
      }

      if (max && index === max) {
        return h(MpMoreAvatarLabel, {
          props: {
            size: this.size || this.groupSize,
            label: `+${count - max}`
          },
          on: {
            click: this.handleMoreClick
          }
        });
      }
    });
    return h(MpFlex, {
      props: {
        alignItems: 'center'
      },
      attrs: {
        zIndex: 0,
        'data-pixel-component': 'MpAvatarGroup'
      }
    }, cloneAllElement);
  }

};

export { MpAvatar, MpAvatarBadge, MpAvatarGroup };
