import { createStyledAttrsMixin, StringArray, SNA, cloneVNode } from '@mekari/pixel-utils';
import { MpFlex } from '@mekari/pixel-flex';
import { MpBox } from '@mekari/pixel-box';

/**
 * Usage :** ` import { MpStack } from '@mekari/pixel' `
 **/

const MpStack = {
  name: 'MpStack',
  mixins: [createStyledAttrsMixin('MpStack')],
  props: {
    direction: [String, Array],
    isInline: {
      type: Boolean,
      default: false
    },
    isReversed: {
      type: Boolean,
      default: false
    },
    align: StringArray,
    justify: StringArray,
    spacing: {
      type: SNA,
      default: 2
    },
    shouldWrapChildren: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    _isInline() {
      return this.isInline || this.direction && this.direction.startsWith('row');
    },

    _isReversed() {
      return this.isReversed || this.direction && this.direction.endsWith('reverse');
    },

    _direction() {
      let _direction;

      if (this._isInline) {
        _direction = 'row';
      }

      if (this._isReversed) {
        _direction = this.isInline ? 'row-reverse' : 'column-reverse';
      }

      if (this.direction) {
        _direction = this.direction;
      }

      if (!this._isInline && !this._isReversed && !this.direction) {
        _direction = 'column';
      }

      return _direction;
    }

  },

  render(h) {
    const defaultSlot = this.$slots.default || [];
    const children = defaultSlot.filter(e => e.tag);
    const stackables = children.map((node, index) => {
      const isLastChild = children.length === index + 1;
      const spacingProps = this._isInline ? {
        [this._isReversed ? 'ml' : 'mr']: isLastChild ? null : this.spacing
      } : {
        [this._isReversed ? 'mt' : 'mb']: isLastChild ? null : this.spacing
      };
      let clone = cloneVNode(node, h);

      if (!clone.componentOptions) {
        clone = h(MpBox, [clone]);
      }

      const {
        propsData
      } = clone.componentOptions;
      const {
        attrs
      } = clone.data; // If children nodes should wrap,
      // we wrap them inside block with
      // display set to inline block.

      if (this.shouldWrapChildren) {
        return h(MpBox, {
          props: {
            as: this.as,
            to: this.to
          },
          attrs: {
            d: 'inline-block',
            ...spacingProps
          }
        }, [clone]);
      } // Otherwise we simply set spacing props
      // to current node.


      clone.componentOptions.propsData = { ...propsData
      };
      clone.data.attrs = { ...attrs,
        ...spacingProps
      };
      return clone;
    });
    return h(MpFlex, {
      class: this.className,
      props: {
        as: this.as,
        align: this.align,
        justify: this.justify,
        direction: this._direction
      },
      attrs: this.computedAttrs,
      on: this.computedListeners
    }, stackables);
  }

};

export { MpStack };
