import { MpBox } from '@mekari/pixel-box';
import { MpButtonIcon } from '@mekari/pixel-button-icon';
import { MpIcon } from '@mekari/pixel-icon';
import { createStyledAttrsMixin } from '@mekari/pixel-utils';

/**
 * Exported select properties.
 *
 * @prop {string|object} as - Component used for root node.
 * @prop {number|string} value - Value of select.
 * @prop {number|string} defaultValue - Initial value of select.
 * @prop {string} placeholder - Placeholder of select.
 * @prop {string} size - Size of select.
 * @prop {boolean} isDisabled - If true, select will be disabled.
 * @prop {boolean} isInvalid - If true, select is marked as invalid.
 * @prop {boolean} isRequired - If true, select will be required.
 * @prop {boolean} isFullWidth - If true, select will span full width of it parent.
 * @prop {boolean} isClearable - If true, allow to remove select content with clear icon.
 * @prop {string} ariaLabel - Optional prop for accessibility.
 * @prop {string} ariaLabelledBy - Optional prop for accessibility.
 */
const useSelectProps = {
  as: {
    type: [String, Object],
    default: 'select'
  },
  value: [Number, String],
  defaultValue: [Number, String],
  placeholder: [String],
  size: {
    type: [String],
    default: 'md'
  },
  isDisabled: {
    type: [Boolean],
    default: false
  },
  isInvalid: {
    type: [Boolean],
    default: false
  },
  isRequired: {
    type: [Boolean],
    default: false
  },
  isFullWidth: {
    type: [Boolean],
    default: true
  },
  isClearable: {
    type: [Boolean],
    default: false
  },
  ariaLabel: [String],
  ariaLabelledBy: [String]
};

/**
 * Exported select styles.
 */
const baseStyles = {
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  outline: '0',
  borderWidth: '1px',
  borderColor: 'inherit',
  borderRadius: 'sm',
  appearance: 'none',
  textOverflow: 'ellipsis',
  transition: 'all 250ms'
};

const sizeStyles = ({
  size,
  isFullWidth,
  isClearable
}) => {
  const variantStyles = {
    md: {
      width: isFullWidth ? '100%' : undefined,
      height: '9.5',
      minWidth: '22',
      fontSize: 'md',
      fontWeight: 'regular',
      lineHeight: 'md',
      borderRadius: 'md',
      paddingLeft: '3',
      paddingRight: isClearable ? '13' : '11',
      paddingY: '2'
    },
    sm: {
      width: isFullWidth ? '100%' : undefined,
      height: '7.5',
      minWidth: '22',
      fontSize: 'sm',
      fontWeight: 'regular',
      lineHeight: 'md',
      borderRadius: 'sm',
      paddingLeft: '3',
      paddingRight: isClearable ? '13' : '9',
      paddingY: '1'
    }
  };

  switch (size) {
    case 'md':
      return variantStyles['md'];

    case 'sm':
      return variantStyles['sm'];

    default:
      return variantStyles['md'];
  }
};

const componentStyles = ({
  isHovered,
  currentValue
}) => {
  return {
    color: typeof currentValue === 'undefined' || currentValue === '' ? 'gray.400' : 'dark',
    backgroundColor: 'transparent',
    borderColor: isHovered ? 'gray.400' : 'gray.100',
    _hover: {
      borderColor: 'gray.400'
    },
    _focus: {
      borderColor: 'blue.500',
      boxShadow: 'outer'
    },
    _disabled: {
      color: 'gray.400',
      backgroundColor: 'gray.50',
      borderColor: 'gray.100',
      cursor: 'not-allowed'
    },
    _invalid: {
      borderColor: 'red.400'
    }
  };
};

const useSelectStyles = props => {
  return { ...baseStyles,
    ...sizeStyles(props),
    ...componentStyles(props)
  };
};

/**
 * Offers a simple form control to select from many options component.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-select
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-select
 */

const MpSelect = {
  name: 'MpSelect',
  mixins: [createStyledAttrsMixin('MpSelect')],
  inject: {
    $useFormControl: {
      default: null
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: { ...useSelectProps
  },

  data() {
    return {
      currentValue: this.value || this.defaultValue,
      isHovered: false
    };
  },

  computed: {
    getFormControls() {
      if (!this.$useFormControl) {
        return {
          controlId: this.controlId,
          isDisabled: this.isDisabled,
          isInvalid: this.isInvalid,
          isRequired: this.isRequired
        };
      }

      return this.$useFormControl(this.$props);
    },

    componentStyles() {
      return useSelectStyles({
        size: this.size,
        isHovered: this.isHovered,
        isFullWidth: this.isFullWidth,
        isClearable: this.isClearable,
        currentValue: this.currentValue
      });
    }

  },
  watch: {
    value(e) {
      this.currentValue = e;
    }

  },
  methods: {
    handleChange(e) {
      this.currentValue = e.target.value;
      this.$emit('change', e.target.value);
    },

    handleClear(e) {
      this.currentValue = null;
      this.handleChange(e);
    }

  },

  render(h) {
    const controlId = this.getFormControls.controlId;
    return h(MpBox, {
      attrs: {
        position: 'relative',
        display: 'inline-flex',
        alignItems: 'center',
        width: this.isFullWidth ? '100%' : undefined
      },
      on: {
        mouseenter: () => {
          this.isHovered = true;
        },
        mouseleave: () => {
          this.isHovered = false;
        }
      }
    }, [this.isClearable && h(MpButtonIcon, {
      props: {
        name: 'reset',
        size: 'sm',
        isHoverable: false
      },
      style: {
        position: 'absolute',
        right: '36px',
        width: '20px',
        height: '20px',
        minWidth: '20px',
        padding: '0',
        visibility: this.currentValue && this.isHovered ? 'visible' : 'hidden',
        opacity: this.currentValue && this.isHovered ? '1' : '0',
        zIndex: '1',
        transition: 'all 250ms'
      },
      on: {
        click: this.handleClear
      }
    }), h(this.as, {
      class: [this.className],
      domProps: {
        value: this.currentValue
      },
      attrs: {
        placeholder: this.placeholder,
        id: controlId,
        disabled: this.getFormControls.isDisabled,
        required: this.getFormControls.isRequired,
        'aria-disabled': this.getFormControls.isDisabled,
        'aria-invalid': this.getFormControls.isInvalid,
        'aria-required': this.getFormControls.isRequired,
        'aria-label': this.ariaLabel || controlId && `${controlId}-control-label`,
        'aria-labelledby': this.ariaLabelledBy || controlId && `${controlId}-help-text`,
        ...this.computedAttrs
      },
      on: {
        change: this.handleChange
      }
    }, [this.placeholder && h('option', {
      attrs: {
        value: ''
      }
    }, this.placeholder), this.$slots.default]), h(MpBox, {
      attrs: {
        position: 'absolute',
        display: 'inline-flex',
        alignItems: 'center',
        width: '1.5rem',
        height: '1.5rem',
        right: '0.5rem',
        whiteSpace: 'nowrap',
        pointerEvents: 'none'
      }
    }, [h(MpIcon, {
      props: {
        name: 'chevrons-down',
        size: 'sm'
      },
      attrs: {
        focusable: false
      }
    })])]);
  }

};

export { MpSelect };
