import { MpPseudoBox } from '@mekari/pixel-pseudo-box';
import { MpIcon } from '@mekari/pixel-icon';
import { createStyledAttrsMixin } from '@mekari/pixel-utils';

/**
 * Exported badge properties.
 *
 * @prop {string} variant - Variant of badge.
 * @prop {string} variantColor - Variant color of badge.
 * @prop {string} size - Size of badge.
 * @prop {string} icon - Displayed icon on left.
 */
const useBadgeProps = {
  variant: {
    type: [String],
    default: 'solid'
  },
  variantColor: {
    type: [String],
    default: 'blue'
  },
  size: {
    type: [String],
    default: 'md'
  },
  icon: [String]
};

/**
 * Exported badge styles.
 */
const baseStyles = {
  display: 'inline-flex',
  alignItems: 'center',
  borderRadius: 'full'
};
const sizesStyles = {
  sm: {
    fontFamily: 'body',
    fontSize: 'xs',
    fontWeight: 'semibold',
    letterSpacing: 'normal',
    height: '4'
  },
  md: {
    fontFamily: 'body',
    fontSize: 'md',
    fontWeight: 'regular',
    letterSpacing: 'normal',
    height: '5'
  }
};

const componentStyles = ({
  variant,
  color,
  size
}) => {
  const getSolidColor = color => {
    if (color === 'blue') return 'sky.400';
    if (color === 'green') return 'teal.400';
    if (color === 'orange') return 'amber.400';
    if (color === 'red') return 'rose.400';
    if (color === 'gray') return 'stone.400';
    return '';
  };

  const getSubtleColor = color => {
    if (color === 'blue') return 'blue.50';
    if (color === 'green') return 'green.50';
    if (color === 'orange') return 'orange.50';
    if (color === 'red') return 'red.50';
    if (color === 'gray') return 'gray.50';
    return '';
  };

  const variantStyles = {
    solid: {
      color: 'white',
      // fontSize: size === 'md' ? 'sm' : 'xs',
      // lineHeight: size === 'md' ? 'md' : '1sm',
      backgroundColor: getSolidColor(color),
      paddingY: size === 'md' ? '0.5' : '0.5',
      paddingX: size === 'md' ? '1.5' : '1'
    },
    subtle: {
      color: color === 'gray' ? 'dark' : `${color}.700`,
      backgroundColor: getSubtleColor(color),
      paddingY: size === 'md' ? '0' : '0',
      paddingX: size === 'md' ? '2' : '1'
    },
    ghost: {
      color: 'dark',
      backgroundColor: 'transparent',
      paddingY: '0',
      paddingX: '0'
    }
  };

  switch (variant) {
    case 'solid':
      return variantStyles['solid'];

    case 'subtle':
      return variantStyles['subtle'];

    case 'ghost':
      return variantStyles['ghost'];

    default:
      return variantStyles['solid'];
  }
};

const useBadgeStyle = props => {
  return { ...baseStyles,
    ...sizesStyles[props.size],
    ...componentStyles(props)
  };
};

/**
 * Badges are used to highlight an item's status for quick recognition.
 *
 * @category Data display - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-display-badge
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-badge
 */

const MpBadge = {
  name: 'MpBadge',
  mixins: [createStyledAttrsMixin('MpBadge')],
  props: { ...useBadgeProps
  },
  computed: {
    componentStyles() {
      return useBadgeStyle({
        variant: this.variant || 'solid',
        color: this.variantColor || 'blue',
        size: this.size || 'md'
      });
    }

  },

  created() {
    if (typeof this.$props.icon !== 'undefined') {
      console.warn('[Pixel error]: [MpBadge] Deprecated on `icon` prop.');
    }

    if (this.$props.variant === 'ghost') {
      console.warn('[Pixel error]: [MpBadge] Deprecated on `variant` prop with `ghost` value.');
    }
  },

  render(h) {
    return h(MpPseudoBox, {
      class: this.className,
      props: {
        as: 'span'
      },
      attrs: { ...this.computedAttrs
      }
    }, [!this.isClosable && this.icon && h(MpPseudoBox, {
      props: {
        as: 'span'
      },
      attrs: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        marginRight: '2',
        'data-pixel-component': 'MpBadgeIcon'
      }
    }, [h(MpIcon, {
      props: {
        name: this.icon,
        size: 'sm',
        variant: 'duotone',
        color: 'currentColor'
      },
      attrs: {
        focusable: false
      }
    })]), this.$slots.default]);
  }

};

export { MpBadge };
