    if (scrollBehavior === 'outside') {
      wrapperStyle = { ...wrapperStyle,
        overflowY: 'auto',
        overflowX: 'hidden'
      };
      contentStyle = { ...contentStyle,
        my: '3.75rem',
        top: 0
      };
    }

