import { createStyledAttrsMixin } from '@mekari/pixel-utils';

/**
 * Usage :** ` import { MpPseudoBox } from '@mekari/pixel' `
 **/

const MpPseudoBox = {
  name: 'MpPseudoBox',
  mixins: [createStyledAttrsMixin('MpPseudoBox', true)],
  props: {
    as: {
      type: [String, Object],
      default: () => 'div'
    },
    to: [String, Object]
  },

  render(h) {
    return h(this.as, {
      props: {
        to: this.to
      },
      class: this.className,
      on: this.listeners$,
      attrs: this.computedAttrs
    }, this.$slots.default);
  }

};

export { MpPseudoBox };
