import { createStyledAttrsMixin, HTMLElement } from '@mekari/pixel-utils';
import { MpModal, MpModalContent, MpModalOverlay, MpModalCloseButton, MpModalBody, MpModalHeader, MpModalFooter } from '@mekari/pixel-modal';

const MpDrawer = {
  name: 'MpDrawer',
  mixins: [createStyledAttrsMixin('MpDrawer')],
  props: {
    isOpen: {
      type: Boolean,
      default: false
    },
    onClose: {
      type: Function,
      default: () => null
    },
    closeOnEsc: {
      type: Boolean,
      default: true
    },
    isFullHeight: {
      type: Boolean,
      default: true
    },
    placement: {
      type: String,
      default: 'right'
    },
    initialFocusRef: {
      type: [HTMLElement, Object, String, Function],
      default: () => null
    },
    finalFocusRef: {
      type: [HTMLElement, Object, String, Function],
      default: () => null
    },
    size: {
      type: String,
      default: 'sm'
    },
    disablePortal: Boolean
  },

  provide() {
    return {
      $DrawerContext: () => this.DrawerContext
    };
  },

  computed: {
    DrawerContext() {
      return {
        size: this.size,
        isOpen: this.isOpen,
        placement: this.placement,
        isFullHeight: this.isFullHeight
      };
    }

  },

  render(h) {
    return h(MpModal, {
      class: [this.className],
      props: {
        size: this.size,
        isOpen: this.isOpen,
        onClose: this.onClose,
        closeOnEsc: this.closeOnEsc,
        initialFocusRef: this.initialFocusRef,
        finalFocusRef: this.finalFocusRef,
        disablePortal: this.disablePortal,
        formatIds: id => ({
          content: `drawer-${id}`,
          header: `drawer-${id}-header`,
          body: `drawer-${id}-body`
        })
      },
      on: this.computedListeners,
      attrs: { ...this.computedAttrs,
        ...this.$attrs
      }
    }, this.$slots.default);
  }

};

const getPlacementStyles = (position, {
  finalWidth,
  finalHeight
}) => {
  const placements = {
    bottom: {
      maxWidth: '100vw',
      height: 'auto',
      bottom: 0,
      left: 0,
      right: 0
    },
    top: {
      maxWidth: '100vw',
      height: 'auto',
      top: 0,
      left: 0,
      right: 0
    },
    left: { ...(finalWidth && {
        maxWidth: finalWidth
      }),
      height: '100vh',
      left: 0,
      top: 0
    },
    right: { ...(finalWidth && {
        maxWidth: finalWidth
      }),
      right: 0,
      top: 0,
      height: '100vh'
    }
  };
  return placements[position] || placements.right;
};

const MpDrawerContent = {
  name: 'MpDrawerContent',
  inheritAttrs: false,
  inject: ['$DrawerContext'],
  computed: {
    context() {
      return this.$DrawerContext();
    },

    placementStyles() {
      const {
        placement,
        isFullHeight
      } = this.context;
      const placementStyles = getPlacementStyles(placement, {
        finalHeight: isFullHeight ? '100vh' : 'auto'
      });
      return {
        position: 'fixed',
        ...placementStyles
      };
    }

  },

  render(h) {
    return h(MpModalContent, {
      props: {
        noStyles: true,
        zIndex: 10000
      },
      attrs: { ...this.placementStyles,
        ...this.$attrs,
        borderRadius: 0,
        'data-pixel-component': 'MpDrawerContent'
      }
    }, this.$slots.default);
  }

};
const MpDrawerOverlay = {
  name: 'MpDrawerOverlay',
  functional: true,

  render(h, {
    data,
    ...rest
  }) {
    return h(MpModalOverlay, { ...rest,
      attrs: { ...data.attrs,
        'data-pixel-component': 'MpDrawerOverlay'
      }
    });
  }

};
const MpDrawerCloseButton = {
  name: 'MpDrawerCloseButton',
  functional: true,

  render(h, {
    data,
    listeners
  }) {
    return h(MpModalCloseButton, {
      nativeOn: { ...listeners,
        click: e => {
          const emitClick = listeners.click;

          if (emitClick) {
            emitClick('click', e);
          }
        }
      },
      attrs: {
        zIndex: '1',
        ...data.attrs,
        'data-pixel-component': 'MpDrawerCloseButton'
      }
    });
  }

};
const MpDrawerBody = { ...MpModalBody,
  name: 'MpDrawerBody'
};
const MpDrawerHeader = { ...MpModalHeader,
  name: 'MpDrawerHeader'
};
const MpDrawerFooter = { ...MpModalFooter,
  name: 'MpDrawerFooter'
};

export { MpDrawer, MpDrawerBody, MpDrawerCloseButton, MpDrawerContent, MpDrawerFooter, MpDrawerHeader, MpDrawerOverlay };
