import { MpButton } from '@mekari/pixel-button';
import { MpIcon } from '@mekari/pixel-icon';
import { createStyledAttrsMixin } from '@mekari/pixel-utils';

/**
 * Exported button icon properties.
 *
 * @prop {string|object} as - Component used for root node.
 * @prop {string|object} to - Link to open when button icon is clicked.
 * @prop {string} type - Type of button icon.
 * @prop {string} size - Size of button icon.
 * @prop {string} icon - Displayed icon on center. **deprecated**
 * @prop {string} name - Displayed icon on center.
 * @prop {string} variant - Variant of button icon.
 * @prop {boolean} isDisabled - If true, button icon will be disabled.
 * @prop {boolean} isActive - If true, button icon will be active.
 * @prop {boolean} isRound - If true, button icon will be rounded. **deprecated**
 * @prop {boolean} isHoverable - If true, button icon hover style will shown.
 * @prop {string} ariaLabel - Optional prop for accessibility.
 * @prop {string} ariaLabelledBy - Optional prop for accessibility.
 */
const useButtonIconProps = {
  as: {
    type: [String, Object],
    default: 'button'
  },
  to: [String, Object],
  type: {
    type: [String],
    default: 'button'
  },
  size: {
    type: [String],
    default: 'sm'
  },
  icon: [String],
  // **deprecated**
  name: [String],
  variant: {
    type: [String],
    default: 'outline'
  },
  isDisabled: {
    type: [Boolean],
    default: false
  },
  isActive: {
    type: [Boolean],
    default: false
  },
  isRound: {
    type: [Boolean],
    default: undefined
  },
  // **deprecated**
  isHoverable: {
    type: [Boolean],
    default: true
  },
  ariaLabel: [String],
  ariaLabelledBy: [String]
};

/**
 * Exported button icon styles.
 */
const componentStyles = ({
  isHoverable
}) => {
  return {
    color: 'gray.600',
    backgroundColor: 'transparent',
    borderColor: 'transparent',
    borderRadius: 'sm',
    outline: '0px solid transparent',
    _hover: isHoverable ? {
      cursor: 'pointer',
      color: 'blue.400',
      backgroundColor: 'ice.50'
    } : {
      cursor: 'pointer',
      color: 'gray.600',
      backgroundColor: 'transparent'
    },
    _focus: {
      boxShadow: 'outer'
    },
    _active: {
      backgroundColor: 'ice.50',
      color: 'blue.400',
      boxShadow: 'inset 0px 2px 6px rgba(0, 0, 0, 0.12)'
    },
    _disabled: {
      backgroundColor: 'white',
      color: 'gray.400',
      cursor: 'not-allowed',
      boxShadow: 'none'
    }
  };
};

const sizeStyles = {
  md: {
    height: '9.5',
    minWidth: '9',
    padding: '1.5'
  },
  sm: {
    height: '7.5',
    minWidth: '7',
    padding: '1'
  }
};
const useButtonIconStyles = props => {
  return { ...sizeStyles[props.size],
    ...componentStyles(props)
  };
};

/**
 * Displays a single, less important action a user can take.
 *
 * @category Data entry - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-data-entry-button-icon
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-button-icon
 */

const MpButtonIcon = {
  name: 'MpButtonIcon',
  mixins: [createStyledAttrsMixin('MpButtonIcon')],
  props: { ...useButtonIconProps
  },
  computed: {
    getButtonIconStyles() {
      return useButtonIconStyles({
        size: this.size,
        isHoverable: this.isHoverable
      });
    }

  },

  created() {
    if (typeof this.$props.icon !== 'undefined') console.error('[Pixel error]: Deprecated on `icon` prop and use `name` instead.');
    if (typeof this.$props.isRound !== 'undefined') console.error('[Pixel error]: Deprecated on `isRound` prop.');
  },

  render(h) {
    return h(MpButton, {
      props: this.$props,
      attrs: { ...this.getButtonIconStyles,
        ...this.computedAttrs,
        ...this.attrs$
      },
      on: this.$listeners
    }, [h(MpIcon, {
      props: {
        name: this.name || this.icon,
        variant: this.isDisabled ? 'outline' : this.variant,
        size: this.size
      },
      attrs: {
        color: 'currentColor',
        focusable: false
      }
    })]);
  }

};

export { MpButtonIcon };
