import { createStyledAttrsMixin } from '@mekari/pixel-utils';

/**
 * Usage :** ` import { MpDivider } from '@mekari/pixel' `
 **/

const MpDivider = {
  name: 'MpDivider',
  mixins: [createStyledAttrsMixin('MpDivider')],
  props: {
    orientation: {
      type: String,
      default: 'horizontal'
    }
  },
  computed: {
    borderProps() {
      return this.orientation === 'vertical' ? {
        borderLeft: '0.0625rem solid',
        height: 'auto',
        mx: 2
      } : {
        borderBottom: '0.0625rem solid',
        width: 'auto',
        my: 2
      };
    },

    componentStyles() {
      return {
        border: 0,
        ...this.borderProps,
        opacity: 0.6,
        borderColor: 'inherit'
      };
    }

  },

  render(h) {
    return h('hr', {
      class: this.className,
      attrs: {
        'aria-orientation': this.orientation,
        ...this.computedAttrs
      }
    });
  }

};

export { MpDivider };
